* {
    margin: 0;
}

.Home {
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    height: 85vh;
    padding: 10px;
    padding-top: 8vh;

}

.Home h1 {
    font-size: 50px;
    padding: 30px 0;
}

.Home p {
    font-size: 18px;
    word-spacing: 3px;
}

.Home img {
    width: 60px;
}