* {
    box-sizing: border-box;
}

nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #61dafb;
    height: 8vh;
    /* background: rgba(40, 44, 52, .8); */
    background: rgba(255, 255, 255, 0.15);
    backdrop-filter: blur(6.2px);
    -webkit-backdrop-filter: blur(6.2px);
    position: absolute;
    width: 100%;
}

.logo {
    padding: 10px;
}

.logo img {
    width: 80px;
}

.navigate {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    justify-self: end;
}

.navigate .link {
    display: inline-block;
    padding: 10px 20px;
    text-decoration: none;
    border-radius: 3px;
    color: #61dafb;
    text-shadow: 2px 0 rgba(40, 44, 52, .5), -2px 0 rgba(40, 44, 52, .5), 0 2px rgba(40, 44, 52, .5), 0 -2px rgba(40, 44, 52, .5),
        1px 1px rgba(40, 44, 52, .5), -1px -1px rgba(40, 44, 52, .5), 1px -1px rgba(40, 44, 52, .5), -1px 1px rgba(40, 44, 52, .5);
}

@media (prefers-reduced-motion: no-preference) {
    .logo img {
        animation: spin infinite 10s linear;
    }
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}