.Card {
    display: grid;
    grid-template-columns: 250px auto;
    grid-template-rows: 200px auto;
    grid-template-areas: 'foto text1'
        'foto text2';
    gap: 10px;
    width: 550px;
    padding: 5px;
    margin: 20px 0;
}

.Card .text1 h3,
.Card .text1 p,
.Card .text2 h2 {
    text-shadow: 2px 0 rgba(40, 44, 52, .5), -2px 0 rgba(40, 44, 52, .5), 0 2px rgba(40, 44, 52, .5), 0 -2px rgba(40, 44, 52, .5),
        1px 1px rgba(40, 44, 52, .5), -1px -1px rgba(40, 44, 52, .5), 1px -1px rgba(40, 44, 52, .5), -1px 1px rgba(40, 44, 52, .5);
}

.Card .foto {
    grid-area: foto;
}

.Card .foto img {
    width: 250px;
    height: 250px;
    object-fit: cover;
    object-position: 0 -20px;
    border-radius: 16px;
    border: 1px solid rgba(97, 218, 251, 0.51);
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.5);
}

.Card .text1 {
    grid-area: text1;
    background: rgba(255, 255, 255, 0.15);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(6.2px);
    -webkit-backdrop-filter: blur(6.2px);
    border: 1px solid rgba(97, 218, 251, 0.51);
}

.Card .text1 h3 {
    font-weight: 600;
    margin-bottom: 10px;
}

.Card .text2 {
    grid-area: text2;
    background: rgba(255, 255, 255, 0.15);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(6.2px);
    -webkit-backdrop-filter: blur(6.2px);
    border: 1px solid rgba(97, 218, 251, 0.51);
    text-decoration: none;
    padding: 10px 20px;
    width: 110px;
    place-self: center;
}

.Card .text2:hover {
    background: rgba(255, 255, 255, 0.2);
}

.Card .text2 h2 {
    color: rgb(97, 218, 251);
    font-weight: 500;
    font-size: 20px;
}

.Card .text1,
.Card .text2 {
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
}

.Card .text1 ul li,
.Card .text2 ul li {
    list-style: none;
}

@media only screen and (max-width: 600px) {
    .Card {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr 1fr;
        grid-template-areas: 'foto'
            'text1'
            'text2';
    }
}