.About {
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    height: 85vh;
    padding: 10px;
}

.About p {
    font-size: 18px;
    word-spacing: 3px;
}

.About img {
    width: 300px;
    border-radius: 50%;
    border: 1px solid #61dafb;
    margin-bottom: 30px;
}

.About h1 {
    margin-top: 30px;
}