.loading {
    display: flex;
    height: 85vh;
    width: 100%;
    flex-flow: column;
    justify-content: center;
    align-items: center;
}

.loading img {
    width: 300px;
    animation: spin infinite 20 linear;
}

@media (prefers-reduced-motion: no-preference) {
    .loading img {
        animation: spin infinite 3s linear;
    }
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}